import React, { Suspense, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Route, Switch, withRouter } from 'react-router-dom';

import DashboardLayout from './DashboardLayout';
import ErrorLayout from './ErrorLayout';
const AuthBasicLayout = React.lazy(() => import('./AuthBasicLayout'));
const Landing = React.lazy(() => import('../components/landing/Landing'));
const AuthCardRoutes = React.lazy(() => import('../components/auth/card/AuthCardRoutes'));
const AuthSplitRoutes = React.lazy(() => import('../components/auth/split/AuthSplitRoutes'));

const Layout = ({ location }) => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location.pathname]);

  return (
    <Suspense fallback={<span />}>
      <Switch>
        <Route path="/landing" exact component={Landing} />
        <Route path="/authentication/basic" component={AuthBasicLayout} />
        <Route path="/authentication/card" component={AuthCardRoutes} />
        <Route path="/authentication/split" component={AuthSplitRoutes} />
        <Route path="/errors" component={ErrorLayout} />
        <Route component={DashboardLayout} />
      </Switch>
    </Suspense>
  );
};

Layout.propTypes = { location: PropTypes.object.isRequired };

export default withRouter(Layout);
